.game-area {
  position: relative;
  margin: 50px auto;
  width: 400px;
  height: 400px;
  border: 10px solid rgba(50, 250, 0, 0.651);
  background-image: url("./grass.jpeg");
}

.snake-dot {
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: rgb(0, 4, 255);
  border-radius: 50%;
  border: 1px solid white;
  z-index: 2;
}

.snake-food {
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: red;
  border-radius: 50%;
  border: 1px solid white;
  z-index: 1;
}


.snake {
  text-align: center;
  width: 100%;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.score {
  width: fit-content;
  margin: 0;
  border: none;
}

h1 {
  margin: 0;
}

.game-area .score {
  float: left;
}

.select {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: fit-content;
  padding: 40px;
  border: 1px solid black;
  border-radius: 5px;
  background-color: rgb(0, 4, 255);
  color: white;
}

.difficulty {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: fit-content;
  padding: 10px;
  border: 1px solid white;
  border-radius: 5px;
  color: white;
  flex-wrap: wrap;
}

button {
  margin: 0 auto;
  width: 100%;
  padding: 10px;
  border: none;
  background-color: transparent;
  color: white;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}

button:hover {
  color: greenyellow;
}